<template><section>
<h2 id="shuo-ming"><a class="header-anchor" href="#shuo-ming" aria-hidden="true">¶</a> 说明</h2>
<p>基于element-ui控件的表单组件封装，抽取配置，统一接口，做成标准的v-model交互控件。</p>
<p>这个组件的npm完整名称是<code v-pre="">@lefreet/vue-form</code>，因为名称被人占用了，我也不想取别的名字。与<a href="https://www.npmjs.com/package/vue-form">vue-form</a>相比，<code v-pre="">@lefreet/vue-form</code>更关注基础表单的简单调用和取值。</p>
<p>简单封装：</p>
<ul>
<li><code v-pre="">input</code> 文本框</li>
</ul>
<p>功能扩展：</p>
<ul>
<li><code v-pre="">tree-select</code> 树形下拉框</li>
<li><code v-pre="">richtext</code> 富文本框(quill)</li>
</ul>
<h2 id="shi-yong"><a class="header-anchor" href="#shi-yong" aria-hidden="true">¶</a> 使用</h2>
<pre v-pre=""><code class="hljs language-bash"><span class="hljs-comment"># install</span>
npm install @lefreet/vue-form --save
</code></pre>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"app"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vue-form</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">""</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"values"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-form</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vue-input</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">""</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> VueForm, { VueInput } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-form'</span>

  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueForm, VueInput },
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">values</span>: {},
        <span class="hljs-attr">input</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<p>所有控件都只接收两个参数:</p>
<ul>
<li>options: 简单的控件一般就是element原控件的参数，只不过被抽取成了键值对<code v-pre="">{}</code>，方便配置注入。如有变更，会在下文demo中说明。</li>
<li>v-model: 控件对应的双向绑定值，不了解的<a href="https://cn.vuejs.org/v2/guide/forms.html">请点击</a>。</li>
<li>控件单独使用时，原控件<code v-pre="">slot</code>,<code v-pre="">methods</code>,<code v-pre="">event</code>都是可以直接调用的。</li>
</ul>
<h2 id="geng-duo-li-zi"><a class="header-anchor" href="#geng-duo-li-zi" aria-hidden="true">¶</a> 更多例子</h2>
<p>为了节约空间（是我太懒），下文demo组件引用和注入都省略。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> { VueXXX } <span class="hljs-keyword">from</span> <span class="hljs-string">'@lefreet/vue-form'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueXXX }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h3 id="input-shu-ru-kuang"><a class="header-anchor" href="#input-shu-ru-kuang" aria-hidden="true">¶</a> input 输入框</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-input :options=\&quot;{}\&quot; v-model=\&quot;input\&quot;&gt;&lt;/vue-input&gt;\n&lt;p&gt;{{ input }}&lt;/p&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  export default {\n    data () {\n      return {\n        input: 'this is a input component.'\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-input :options="{}" v-model="input"></vue-input>
<p>{{ input }}</p>


</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-input</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"{}"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{ input }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">input</span>: <span class="hljs-string">'this is a input component.'</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<p><code v-pre="">options</code>配置参考，<a href="http://element.eleme.io/#/zh-CN/component/input">请点击</a></p>
<h3 id="radio-dan-xuan-kuang"><a class="header-anchor" href="#radio-dan-xuan-kuang" aria-hidden="true">¶</a> radio 单选框</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-radio :options=\&quot;radioOptions\&quot; v-model=\&quot;radio\&quot;&gt;&lt;/vue-radio&gt;\n&lt;p&gt;this radio's value is {{ radio }}&lt;/p&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  export default {\n    data () {\n      return {\n        radio: 'apple'\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-radio :options="radioOptions" v-model="radio"></vue-radio>
<p>this radio's value is {{ radio }}</p>


</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-radio</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"radioOptions"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>this radio's value is {{ radio }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radio</span>: <span class="hljs-string">'apple'</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<p>用<code v-pre="">el-radio-group</code>标签对<code v-pre="">el-radio</code>和<code v-pre="">el-radio-button</code>做了包装</p>
<h4 id="options-kuo-zhan-can-shu"><a class="header-anchor" href="#options-kuo-zhan-can-shu" aria-hidden="true">¶</a> options扩展参数</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>示例</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>radios</td>
<td><code v-pre="">el-radio</code>标签的枚举数组, 对象内容为该标签的配置</td>
<td><code v-pre="">[{text: '是', label: '1'}, {text: '否', label: '0'}]</code></td>
<td>undefined</td>
</tr>
<tr>
<td>radio-buttons</td>
<td><code v-pre="">el-radio-button</code>...同上</td>
<td>同上</td>
<td>同上</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import VueForm, { VueInput, VueRadio } from '../../index.js'

  export default {
    components: {
      VueInput,
      VueRadio
    },
    data () {
      return {
        inputOptions: {},
        input: 'this is a input component.',
        radioOptions: {
          radios: [{
            'text': '苹果',
            'label': 'apple'
          }, {
            'text': '橘子',
            'label': 'orange'
          }]
        },
        radio: 'apple'
      }
    }
  }
</script>


